import React from "react";
import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <div className="bg-grayNido">
      <div className="text-white font-poppins pt-10 pb-10 pl-4 pr-4 flex flex-row sm:w-160 sm:mx-auto lg:w-180">
        {/* Logo */}
        <div className="w-1/2 sm:w-2/5 flex flex-wrap justify-center content-center pr-4 lg:pr-4">
          {/* nido isotipo */}
          <Link to="/">
            <img
              className="w-32 inline-block align-middle mx-auto sm:hidden"
              src="https://i.imgur.com/ZL6O7w3.png"
              alt="Isotipo Nido"
            />
          </Link>

          {/* nido isologo */}
          <Link to="/">
            <img
              className="w-48 inline-block align-middle mx-auto hidden sm:flex"
              src="https://i.imgur.com/tAsO89r.png"
              alt="Isologo Nido"
            />
          </Link>
        </div>
        {/* Nav */}
        <div className="w-1/2 sm:w-3/5 h-auto pl-1 sm:flex sm:flex-row sm:gap-x-6 lg:gap-x-8 lg:pl-4">
          {/* routes */}
          <div className="pb-4">
            <h4 className="font-bold text-xl pb-2">Navegación</h4>
            <div className="flex flex-row">
              <div className="pr-2">
                <img
                  className="w-"
                  src="https://i.imgur.com/a8WYDEC.png"
                  alt=""
                />
              </div>
              <div className="flex flex-col justify-center content-center">
                <ul className="font-semibold flex flex-col justify-center content-center gap-y-2">
                  {/* inicio */}
                  <Link to="/">
                    <li>Inicio</li>
                  </Link>

                  {/* cursos */}
                  <Link to="/cursos">
                    <li>Cursos</li>
                  </Link>
                  {/* contacto */}
                  <Link to="/contacto">
                    <li>Contacto</li>
                  </Link>
                </ul>
              </div>
            </div>
          </div>

          {/* social media */}
          <div>
            <h4 className="font-bold text-xl pb-1">Redes sociales</h4>
            <div className="flex flex-row gap-x-1 justify-left content-center opacity-80	">
              {/* Instagram */}
              <a
                href="https://instagram.com/cursos.nido"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  className="w-10 h-10"
                  alt="svgImg"
                  src="data:image/svg+xml;base64,"
                />
              </a>

              {/* Twitter */}
              <a
                href="https://twitter.com/Nido69235106"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  className="w-10 h-10"
                  alt="svgImg"
                  src="data:image/svg+xml;base64,"
                />
              </a>

              {/* GitHub */}
              <a
                href="https://github.com/javiermojito/nido"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  className="w-10 h-10"
                  alt="svgImg"
                  src="data:image/svg+xml;base64,"
                />
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Footer;
